:8px}*{box-sizing:border-box}body{background:#1c8ef9 !important;min-height:100vh;display:flex;font-weight:400;font-family:"Fira Sans", sans-serif}h1,h2,h3,h4,h5,h6,label,span{font-weight:500;font-family:"Fira Sans", sans-serif}body,html,.App,#root,.auth-wrapper{width:100%;height:100%}.navbar-light{background-color:#ffffff;box-shadow:0px 14px 80px rgba(34,35,58,0.2)}.auth-wrapper{display:flex;justify-content:center;flex-direction:column;text-align:left}.auth-inner{width:450px;margin:auto;background:#ffffff;box-shadow:0px 14px 80px rgba(34,35,58,0.2);padding:40px 55px 45px 55px;border-radius:15px;transition:all 0.3s}.auth-wrapper .form-control:focus{border-color:#167bff;box-shadow:none}.auth-wrapper h3{text-align:center;margin:0;line-height:1;padding-bottom:20px}.custom-control-label{font-weight:400}.forgot-password,.forgot-password a{text-align:right;font-size:13px;padding-top:10px;color:#7f7d7d;margin:0}.forgot-password a{color:#167bff}.mainbox{margin:auto;background-color:#95c2de;margin:auto;height:600px;width:600px;position:relative}.err{color:#ffffff;font-family:"Nunito Sans", sans-serif;font-size:11rem;position:absolute;left:20%;top:8%}.far{position:absolute;font-size:8.5rem;left:42%;top:15%;color:#ffffff}.err2{color:#ffffff;font-family:"Nunito Sans", sans-serif;font-size:11rem;position:absolute;left:68%;top:8%}.msg{text-align:center;font-family:"Nunito Sans", sans-serif;font-size:1.6rem;position:absolute;left:16%;top:45%;width:75%}a{text-decoration:none;color:white}a:hover{text-decoration:underline}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

